.App {
    text-align: center;
    background-color: white;
}

.add-appointment .card-body {
    display: none;
}

.apt-addheading {
    cursor: pointer;
    user-select: none;
}

.appointment-list {
    font-size: 1.1em;
}

.appointment-list .pet-item {
    border-bottom: 1px dotted gray;
}

.appointment-list .pet-item:last-child {
    border-bottom: none;
}

.appointment-list .pet-name {
    font-weight: 600;
    color: #337ab7;
    font-size: 1.3em;
    line-height: 100%;
}

.appointment-list .label-item {
    font-weight: 600;
    color: #667b82;
}

.appointment-list .apt-date {
    font-style: italic;
}

.appointment-list .apt-notes {
    line-height: 120%;
}

.dropdown-item.active,
.dropdown-item:active {
    background-color: #a9aeb2;
}

